.HelpPage {
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 110px) !important;
}

.HelpPage .ContentArea {
    margin: 25px 0;
}

.HelpPage .helpContentArea {
    display: flex;
}

.HelpPage .helpContentArea .osTypeDropdownButton {
    display: flex;
    margin: unset;
    padding: 10px 15px;
    height: 100%;
    color: #ffffff;
    background-color: #9147FF;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-size: 15pt;
}

.HelpPage .helpContentArea .osTypeDropdownButton:hover {
    background-color: #772ce8;
}

.HelpPage .helpContentArea .osTypeDropdownButton:active {
    background-color: #5c16c5;
}

.HelpPage .osTypeDropdown {
    position: absolute;
    top: 100%;
    right: 0;
    display: block;
    box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.2) !important;
    border-radius: 5px;
    overflow: hidden;
    z-index: 1;
}

.HelpPage .osTypeDropdown .osTypeDropdownOption {
    display: flex;
    padding: 10px 15px;
    color: #ffffff;
    background-color: #9147FF;
    text-decoration: none;
    gap: 10px;
    align-items: center;
    cursor: pointer;
    user-select: none;
}

.HelpPage .osTypeDropdown .osTypeDropdownOption:hover {
    background-color: #772ce8;
}

.HelpPage .osTypeDropdown .osTypeDropdownOption:active {
    background-color: #5c16c5;
}

@media (max-width: 1000px) {
    .HelpPage .helpContentArea .ImageLabel {
        width: 150px !important;
        height: 150px !important;
    }
}

@media (max-width: 600px) {
    .HelpPage .helpContentArea .ImageLabel {
        display: none;
    }
    .HelpPage .helpContentArea .infoArea {
        margin-left: unset !important;
    }
}

.HelpPage .installation .downloadButton {
    margin: 10px 0;
    padding: 10px 15px;
    width: 100%;
    font-size: 15px;
    text-align: center;
    color: white;
    background-color: #00cf00;
    text-decoration: none;
}

.HelpPage .installation .downloadButton:hover {
    background-color: #00b400;
}

.HelpPage .installation .downloadButton:active {
    background-color: #008600;
}

.HelpPage .imageToggler {
    position: fixed;
    display: flex;
    bottom: 30px;
    right: 30px;
    padding: 10px 15px;
    background-color: #ffffff;
    border-radius: 100px;
    box-shadow: 0 0 2px 2px #e5e5e5;
    font-family: 'Nanum Gothic', sans-serif;
    color: black;
    font-size: 14pt;
}